"use client"

import { useState, useRef, useEffect, useCallback } from "react"
import { useRouter } from "next/navigation"
import { PageHeader } from "@/components/layout/page-header"
import { User, FileText, Building2, Clock, Calendar } from "lucide-react"
import { Button } from "@/components/ui/button"
import { Input } from "@/components/ui/input"
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from "@/components/ui/select"
import { Popover, PopoverContent, PopoverTrigger } from "@/components/ui/popover"
import { Calendar as CalendarComponent } from "@/components/ui/calendar"
import { format } from "date-fns"
import { ru } from "date-fns/locale"
import type { User as UserType, UserEditFormData, FormErrors, UserDocument } from "@/types/user"
import { SidebarNav } from "./sidebar-nav"
import { EditSection } from "./edit-section"
import { FormField } from "./form-field"
import { FileUpload } from "./file-upload"
import { ScanPreviewModal } from "../scan-preview-modal"

interface UserEditFormProps {
  user: UserType
}

export function UserEditForm({ user }: UserEditFormProps) {
  const router = useRouter()
  const [activeSection, setActiveSection] = useState("personal")
  const [isSaving, setIsSaving] = useState(false)
  const [previewDocument, setPreviewDocument] = useState<UserDocument | null>(null)
  const [documents, setDocuments] = useState<UserDocument[]>(user.documents)

  // Section refs for intersection observer
  const sectionRefs = {
    personal: useRef<HTMLDivElement>(null),
    passport: useRef<HTMLDivElement>(null),
    other: useRef<HTMLDivElement>(null),
    bank: useRef<HTMLDivElement>(null),
    system: useRef<HTMLDivElement>(null),
  }

  // Form state
  const [formData, setFormData] = useState<UserEditFormData>({
    lastName: user.lastName,
    firstName: user.firstName,
    middleName: user.middleName,
    birthDate: user.birthDate,
    gender: user.gender || "male",
    passport: {
      seriesNumber: `${user.passport.series} ${user.passport.number}`,
      issueDate: user.passport.issueDate,
      issuedBy: user.passport.issuedBy,
      registrationAddress: user.passport.registrationAddress,
    },
    otherData: {
      snils: user.otherData.snils,
      inn: user.otherData.inn,
    },
    bankDetails: {
      cardNumber: user.bankDetails.cardNumber,
      recipient: user.bankDetails.recipient,
      bik: user.bankDetails.bik,
      corrAccount: user.bankDetails.corrAccount,
      accountNumber: user.bankDetails.accountNumber,
    },
    email: user.email,
    status: user.status,
  })

  const [errors, setErrors] = useState<FormErrors>({})

  // Date pickers state
  const [birthDateOpen, setBirthDateOpen] = useState(false)
  const [issueDateOpen, setIssueDateOpen] = useState(false)

  // Intersection Observer for active section
  useEffect(() => {
    const observer = new IntersectionObserver(
      (entries) => {
        entries.forEach((entry) => {
          if (entry.isIntersecting) {
            setActiveSection(entry.target.id)
          }
        })
      },
      { rootMargin: "-100px 0px -70% 0px", threshold: 0 },
    )

    Object.values(sectionRefs).forEach((ref) => {
      if (ref.current) {
        observer.observe(ref.current)
      }
    })

    return () => observer.disconnect()
  }, [])

  const handleNavigate = useCallback((id: string) => {
    const element = document.getElementById(id)
    if (element) {
      element.scrollIntoView({ behavior: "smooth", block: "start" })
    }
  }, [])

  const updateField = useCallback(
    (path: string, value: string) => {
      setFormData((prev) => {
        const keys = path.split(".")
        const newData = { ...prev }
        let current: any = newData

        for (let i = 0; i < keys.length - 1; i++) {
          current[keys[i]] = { ...current[keys[i]] }
          current = current[keys[i]]
        }

        current[keys[keys.length - 1]] = value
        return newData
      })

      // Clear error on change
      if (errors[path]) {
        setErrors((prev) => ({ ...prev, [path]: undefined }))
      }
    },
    [errors],
  )

  const validate = useCallback((): boolean => {
    const newErrors: FormErrors = {}

    if (!formData.lastName.trim()) newErrors.lastName = "Обязательное поле"
    if (!formData.firstName.trim()) newErrors.firstName = "Обязательное поле"
    if (!formData.email.trim()) newErrors.email = "Обязательное поле"
    else if (!/^[^\s@]+@[^\s@]+\.[^\s@]+$/.test(formData.email)) {
      newErrors.email = "Некорректный email"
    }
    if (!formData.passport.seriesNumber.trim()) {
      newErrors["passport.seriesNumber"] = "Обязательное поле"
    }

    setErrors(newErrors)
    return Object.keys(newErrors).length === 0
  }, [formData])

  const handleSave = useCallback(async () => {
    if (!validate()) return

    setIsSaving(true)
    // Simulate save
    await new Promise((resolve) => setTimeout(resolve, 1000))
    setIsSaving(false)
    router.push(`/users/${user.id}`)
  }, [validate, router, user.id])

  const handleCancel = useCallback(() => {
    router.push(`/users/${user.id}`)
  }, [router, user.id])

  return (
    <div className="min-h-screen bg-gray-50">
      <PageHeader
        backButton={{
          href: `/users/${user.id}`,
          label: "К профилю",
        }}
        title="Редактирование профиля"
        description={`${user.lastName} ${user.firstName} ${user.middleName}`}
        actions={
          <>
            <Button variant="outline" onClick={handleCancel}>
              Отмена
            </Button>
            <Button onClick={handleSave} disabled={isSaving} className="bg-primary hover:bg-primary/90">
              {isSaving ? "Сохранение..." : "Сохранить"}
            </Button>
          </>
        }
        maxWidth="7xl"
      />

      {/* Content */}
      <div className="max-w-6xl mx-auto px-6 py-6">
        <div className="flex gap-6">
          {/* Sidebar */}
          <div className="w-56 flex-shrink-0">
            <div className="sticky top-36">
              <SidebarNav activeSection={activeSection} onNavigate={handleNavigate} />
            </div>
          </div>

          {/* Form Sections */}
          <div className="flex-1 space-y-6">
            {/* Personal Data */}
            <EditSection ref={sectionRefs.personal} id="personal" icon={User} title="Персональные данные">
              <div className="grid grid-cols-2 gap-4">
                <FormField label="Фамилия" error={errors.lastName}>
                  <Input
                    value={formData.lastName}
                    onChange={(e) => updateField("lastName", e.target.value)}
                    placeholder="Введите фамилию"
                    className={errors.lastName ? "border-red-500" : ""}
                  />
                </FormField>
                <FormField label="Имя" error={errors.firstName}>
                  <Input
                    value={formData.firstName}
                    onChange={(e) => updateField("firstName", e.target.value)}
                    placeholder="Введите имя"
                    className={errors.firstName ? "border-red-500" : ""}
                  />
                </FormField>
                <FormField label="Отчество">
                  <Input
                    value={formData.middleName}
                    onChange={(e) => updateField("middleName", e.target.value)}
                    placeholder="Введите отчество"
                  />
                </FormField>
                <FormField label="Дата рождения">
                  <Popover open={birthDateOpen} onOpenChange={setBirthDateOpen}>
                    <PopoverTrigger asChild>
                      <Button variant="outline" className="w-full justify-start text-left font-normal bg-transparent">
                        <Calendar className="mr-2 h-4 w-4 text-gray-400" />
                        {formData.birthDate || "Выберите дату"}
                      </Button>
                    </PopoverTrigger>
                    <PopoverContent className="w-auto p-0" align="start">
                      <CalendarComponent
                        mode="single"
                        selected={new Date()}
                        onSelect={(date) => {
                          if (date) {
                            updateField("birthDate", format(date, "d MMMM yyyy", { locale: ru }))
                          }
                          setBirthDateOpen(false)
                        }}
                        locale={ru}
                      />
                    </PopoverContent>
                  </Popover>
                </FormField>
              </div>
              <div className="mt-4">
                <FormField label="Пол">
                  <div className="flex items-center gap-6 mt-2">
                    <label className="flex items-center gap-2 cursor-pointer">
                      <input
                        type="radio"
                        name="gender"
                        checked={formData.gender === "male"}
                        onChange={() => updateField("gender", "male")}
                        className="w-4 h-4 text-gray-900 border-gray-300 focus:ring-gray-500"
                      />
                      <span className="text-sm text-gray-700">Мужской</span>
                    </label>
                    <label className="flex items-center gap-2 cursor-pointer">
                      <input
                        type="radio"
                        name="gender"
                        checked={formData.gender === "female"}
                        onChange={() => updateField("gender", "female")}
                        className="w-4 h-4 text-gray-900 border-gray-300 focus:ring-gray-500"
                      />
                      <span className="text-sm text-gray-700">Женский</span>
                    </label>
                  </div>
                </FormField>
              </div>
            </EditSection>

            {/* Passport Data */}
            <EditSection ref={sectionRefs.passport} id="passport" icon={FileText} title="Паспортные данные">
              <div className="grid grid-cols-2 gap-4">
                <FormField label="Серия и номер" error={errors["passport.seriesNumber"]}>
                  <Input
                    value={formData.passport.seriesNumber}
                    onChange={(e) => updateField("passport.seriesNumber", e.target.value)}
                    placeholder="0000 000000"
                    className={errors["passport.seriesNumber"] ? "border-red-500" : ""}
                  />
                </FormField>
                <FormField label="Дата выдачи">
                  <Popover open={issueDateOpen} onOpenChange={setIssueDateOpen}>
                    <PopoverTrigger asChild>
                      <Button variant="outline" className="w-full justify-start text-left font-normal bg-transparent">
                        <Calendar className="mr-2 h-4 w-4 text-gray-400" />
                        {formData.passport.issueDate || "Выберите дату"}
                      </Button>
                    </PopoverTrigger>
                    <PopoverContent className="w-auto p-0" align="start">
                      <CalendarComponent
                        mode="single"
                        selected={new Date()}
                        onSelect={(date) => {
                          if (date) {
                            updateField("passport.issueDate", format(date, "d MMMM yyyy", { locale: ru }))
                          }
                          setIssueDateOpen(false)
                        }}
                        locale={ru}
                      />
                    </PopoverContent>
                  </Popover>
                </FormField>
                <FormField label="Кем выдан">
                  <Input
                    value={formData.passport.issuedBy}
                    onChange={(e) => updateField("passport.issuedBy", e.target.value)}
                    placeholder="Введите кем выдан"
                  />
                </FormField>
                <FormField label="Место регистрации">
                  <Input
                    value={formData.passport.registrationAddress}
                    onChange={(e) => updateField("passport.registrationAddress", e.target.value)}
                    placeholder="Введите адрес"
                  />
                </FormField>
              </div>
              <div className="mt-6">
                <p className="text-sm font-medium text-gray-700 mb-3">Загруженные документы</p>
                <FileUpload
                  documents={documents}
                  onDocumentsChange={setDocuments}
                  onPreview={setPreviewDocument}
                  category="passport"
                />
              </div>
            </EditSection>

            {/* Other Data */}
            <EditSection ref={sectionRefs.other} id="other" icon={FileText} title="Прочие данные">
              <div className="grid grid-cols-2 gap-4">
                <FormField label="СНИЛС">
                  <Input
                    value={formData.otherData.snils}
                    onChange={(e) => updateField("otherData.snils", e.target.value)}
                    placeholder="000-000-000 00"
                  />
                </FormField>
                <FormField label="ИНН">
                  <Input
                    value={formData.otherData.inn}
                    onChange={(e) => updateField("otherData.inn", e.target.value)}
                    placeholder="000000000000"
                  />
                </FormField>
              </div>
              <div className="mt-6">
                <p className="text-sm font-medium text-gray-700 mb-3">Загруженные документы</p>
                <FileUpload
                  documents={documents}
                  onDocumentsChange={setDocuments}
                  onPreview={setPreviewDocument}
                  category="other"
                />
              </div>
            </EditSection>

            {/* Bank Details */}
            <EditSection ref={sectionRefs.bank} id="bank" icon={Building2} title="Банковские реквизиты">
              <div className="grid grid-cols-2 gap-4">
                <FormField label="Номер карты">
                  <Input
                    value={formData.bankDetails.cardNumber}
                    onChange={(e) => updateField("bankDetails.cardNumber", e.target.value)}
                    placeholder="0000 0000 0000 0000"
                  />
                </FormField>
                <FormField label="Получатель">
                  <Input
                    value={formData.bankDetails.recipient}
                    onChange={(e) => updateField("bankDetails.recipient", e.target.value)}
                    placeholder="ФИО получателя"
                  />
                </FormField>
                <FormField label="БИК">
                  <Input
                    value={formData.bankDetails.bik}
                    onChange={(e) => updateField("bankDetails.bik", e.target.value)}
                    placeholder="000000000"
                  />
                </FormField>
                <FormField label="Корр. счет">
                  <Input
                    value={formData.bankDetails.corrAccount}
                    onChange={(e) => updateField("bankDetails.corrAccount", e.target.value)}
                    placeholder="00000000000000000000"
                  />
                </FormField>
              </div>
              <div className="mt-4">
                <FormField label="Расчетный счет">
                  <Input
                    value={formData.bankDetails.accountNumber}
                    onChange={(e) => updateField("bankDetails.accountNumber", e.target.value)}
                    placeholder="00000000000000000000"
                  />
                </FormField>
              </div>
            </EditSection>

            {/* System Info */}
            <EditSection ref={sectionRefs.system} id="system" icon={Clock} title="Системная информация">
              <div className="grid grid-cols-2 gap-4">
                <FormField label="Email" error={errors.email}>
                  <Input
                    type="email"
                    value={formData.email}
                    onChange={(e) => updateField("email", e.target.value)}
                    placeholder="email@example.com"
                    className={errors.email ? "border-red-500" : ""}
                  />
                </FormField>
                <FormField label="Статус">
                  <Select value={formData.status} onValueChange={(value) => updateField("status", value)}>
                    <SelectTrigger>
                      <SelectValue />
                    </SelectTrigger>
                    <SelectContent>
                      <SelectItem value="active">Активный</SelectItem>
                      <SelectItem value="inactive">Неактивный</SelectItem>
                      <SelectItem value="blocked">Заблокирован</SelectItem>
                    </SelectContent>
                  </Select>
                </FormField>
              </div>
            </EditSection>
          </div>
        </div>
      </div>

      {/* Document Preview Modal */}
      <ScanPreviewModal
        document={previewDocument}
        isOpen={!!previewDocument}
        onClose={() => setPreviewDocument(null)}
      />
    </div>
  )
}
